import React, { useEffect, useState } from 'react';
import ProjectItem from '../ProjectItem';
import axios from 'axios';
import '../../static/css/projects.css'


const Project = () => {
    const [data, setData] = useState({
        projects: [],
        complete: false,
        pending: false,
        error: false
    });

    useEffect(
        () => {
            setData({
                projects: null,
                complete: false,
                pending: true,
                error: false
            })
            axios.get("https://api.github.com/users/treyham91/repos", { params: { sort: 'updated'}})
                .then(res =>
                    setData({
                        projects: res.data,
                        complete: true,
                        pending: false,
                        error: false
                    }),
                )
                .catch(() =>
                    setData({
                        projects: null,
                        complete: false,
                        pending: false,
                        error: true
                    }),
                );
        },
        []
    );

    const { projects, complete, pending, error } = data;

    return (
        <div id="project-container">
            <h1>Projects Overview</h1>
            <div>
                <h3>Freelance Projects:</h3>
                <div>
                    <a href="https://www.6to1designs.com">6to1 Designs</a>
                </div>
            </div>
            <h3>GitHub Projects:</h3>
            {pending ? 'Retrieving Projects...' :
                <div className="project-items">
                        {data.projects.slice(0,5).map(repo => {
                            return (
                                <ProjectItem
                                    key={repo.id}
                                    projectName={repo.name}
                                    projectUrl={repo.html_url}/>
                            )
                        })}
                </div>}
        </div>
    );
}


export default Project;