import React, { useState } from 'react';
import '../../static/css/skills.css'
import database from '../../static/images/server (1).png'
import coding from '../../static/images/file.png'
import chip from '../../static/images/chip (1).png'


const Skills = () => {
    return (
        <div style={{height: '100%'}}>
            <div>
                <h1 style={{color: 'white', fontSize: 50}}>Skills</h1>
            </div>
            <div id="skills-container">
            <div id="databases">
                <img src={database} alt="Database"></img>
                <p style={{fontSize: 20}}>Databases</p>
                <ul>
                    <li>SQL Server</li>
                    <li>Oracle</li>
                    <li>MongoDB</li>
                    <li>SQLite</li>
                    <li>MySQL</li>
                </ul>
            </div>
            <div id="languages">
                <img src={coding} alt="Lanuages"></img>
                <p style={{fontSize: 20}}>Languages</p>
                <ul>
                    <li>Python</li>
                    <li>JavaScript</li>
                    <li>SQL</li>
                    <li>PHP</li>
                    <li>C#</li>
                    <li>TypeScript</li>
                </ul>
            </div>
            <div id="technologies">
                <img src={chip} alt="Technolgies"></img>
                <p style={{fontSize: 20}}>Technologies</p>
                <ul>
                    <li>React.js</li>
                    <li>Node.js</li>
                    <li>Django</li>
                    <li>HTML</li>
                    <li>CSS</li>
                    <li>Express.js</li>
                    <li>GraphQL</li>
                    <li>REST APIs</li>
                    <li>.NET</li>
                </ul>
            </div>
        </div>
        </div>
    )
}

export default Skills;