import React from 'react';
import codepen from '../static/images/logo_codepen.png';
import github from '../static/images/logo_github.png';
import linkedin from '../static/images/logo_likedin.png';
import '../static/css/footer.css';

const Footer = (props) => {

    return (
        <footer>
            <div>
                <ul style={{ display: 'flex', listStyle: 'none', paddingLeft: 0 }}>
                    <li>
                        <a href="https://codepen.io/treyham91">
                            <img src={codepen} alt="Codepen"></img>
                        </a>
                    </li>
                    <li>
                        <a href="https://github.com/treyham91">
                            <img src={github} alt="GitHub"></img>
                        </a>
                    </li>
                    <li>
                        <a href="https://www.linkedin.com/in/trey-hamilton-8739b7126/">
                            <img src={linkedin} alt="LinkedIn"></img>
                        </a>
                    </li>
                </ul>
                Icons made by <a href="https://www.flaticon.com/authors/freepik" title="Freepik">
                    Freepik</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div>
        </footer>
    );
}

export default Footer;